<template>
  <div id="reg">
	  <h1>欢 迎 注 册</h1>
	  <div class="Ri1">
		  <Input val = "请输入账号" item = "../assets/img/zhanghao.png" v-model="username" />
	  </div>
	  <div class="Ri2">
		  <Input val = "请输入密码" item = "../assets/img/mima.png" v-model="password" />
	  </div>
	  <div class="Rb1">
		  <Button text="注 册" @click="reg"/>
		  <router-link
		  				to="/login"
		  				tag="p"
		  >
		  			   已有账号，去登录
		  </router-link>
		   {{errorMess}}
	  </div>
	  
  </div>
</template>

<script>
import Button from '../components/Button.vue'
import Input from '../components/Input.vue'

export default {
  name: 'Reg',
  data() {
    return {
      username:'',
      password:'',
      errorMess:'',
    }
  },
  components: {
    Input,Button
  },
  methods:{
	  reg(){
	    let fromData = new FormData();// js类型
	    fromData.append('username',this.username)
	    fromData.append('password',this.password)
	    
	    axios({
	      url:'/api/reg',
	      method:'post',
	      data:fromData
	    }).then(
	      res => {
	        if(res.data.err===0){
				console.log("111")
	          this.$router.replace('/login')
	        }else{
	          this.errorMess=res.data.msg
	        }
	      }
	    )
	    
	  }
  }
}
</script>

<style>
	#reg{
		position: fixed;
		width: 100%;
		height: 13.34rem;
		background-color: #00B7EE;
	}
	h1{
		text-align: center;
		line-height: 5rem;
		color: #ffffff;
	}
	.Ri1{
		position: relative;
		left: 0.73rem;
	}
	.Ri2{
		position: relative;
		left: 0.73rem;
		top: 0.45rem;
	}
	.Rb1{
		position: relative;
		left: 0.70rem;
		top:1.5rem;
	}
	.Rb1 p{
		margin-top: 0.5rem;
	}
	body{
		background-color: #42B983;
	}
</style>