<template>
  <div id="login">
	  <h1>欢 迎 登 陆</h1>
	  <div class="Li1">
		  <!-- <Input val = "请输入账号" type = "text"  item = "../assets/img/zhanghao.png" :value="username" @input="checkUser" /> -->
		  <Input val = "请输入账号" type = "text"  item = "../assets/img/zhanghao.png" v-model="username" />
	  </div>
	  <div class="Li2">
		  <Input val = "请输入密码" type = "password" v-model="password" />
	  </div>
	  <div class="Lb1">
		  <Button text="登 录" @click="login" />
		   <router-link
				to="/reg"
				tag="p"
		   >
			   没有账号，去注册
		   </router-link>
	  </div>
  </div>
</template>

<script>
import Button from '../components/Button.vue'
import Input from '../components/Input.vue'

export default {
  name: 'Login',
  data(){
    return {
      username:'',
      password:''
    }
	},
  components: {
    Input,Button
  },
  methods:{
	  // checkUser(val){
		 //  this.username = val
	  // },
      login(){
        axios({
          url:'/api/login',
          method:'post',
          data:{
            username:this.username,
            password:this.password
          }
        }).then(
          res=>{
            //种user信息
            window.localStorage.setItem('user',JSON.stringify(res.data))
            
            //跳转之前的页面
            if(this.$route.query.p){
              this.$router.replace(this.$route.query.p)
            }else{
              this.$router.replace('/uer')
            }
            
            
          }
        )
      }
  }
}
</script>

<style>
	#login{
		position: fixed;
		width: 100%;
		height: 13.34rem;
		background-color: #00B7EE;
	}
	#login h1{
		text-align: center;
		line-height: 5rem;
		color: #ffffff;
	}
	#login .Li1{
		position: relative;
		left: 0.73rem;
	}
	#login .Li2{
		position: relative;
		left: 0.73rem;
		top: 0.45rem;
	}
	#login .Lb1{
		position: relative;
		left: 0.70rem;
		top:1.5rem;
	}
	#login .div1{
		display: inline-block;
	}
	#login .img1{
		width: 10%;
	}
	#login .Lb1 p{
		margin-top: 0.5rem;
	}
</style>